.container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100%;
  background-color: #f4f4f4; /* Light grey background */
}

.loginWrapper {
  display: flex;
  width: 80%;
  max-width: 1200px; /* Limit maximum width for larger screens */
  background-color: #fff; /* White background for the login container */
  border-radius: 10px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1); /* Subtle shadow for depth */
  overflow: hidden; /* Ensure the image doesn't overflow */
}

.imageSection {
  flex: 1;
  background-color: #e0e0e0; /* Light grey background for the image section */
  display: flex;
  justify-content: center;
  align-items: center;
}

.imageSection img {
  max-width: 100%;
  max-height: 100%;
  object-fit: cover; /* Ensure the image covers the entire section */
}

.loginSection {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 40px;
  background-color: #fff; /* White background for the login form */
}

h2 {
  margin-bottom: 20px;
  font-size: 24px;
  color: #333;
}

.inputGroup {
  margin-bottom: 15px;
  width: 100%;
  max-width: 300px;
}

.inputGroup label {
  display: block;
  margin-bottom: 5px;
  font-size: 14px;
  color: #555;
}

.inputGroup input,
.inputGroup select {
  width: 100%;
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 14px;
}

.phoneInput {
  display: flex;
  align-items: center;
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 5px;
}

.phoneInput span {
  padding: 0 10px;
  font-size: 14px;
  color: #333;
}

.phoneInput input {
  flex: 1;
  border: none;
  outline: none;
  padding: 8px;
  font-size: 14px;
}

.submitButton {
  width: 100%;
  padding: 10px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 4px;
  font-size: 16px;
  cursor: pointer;
}

.submitButton:hover {
  background-color: #0056b3;
}

.orDivider {
  margin: 20px 0;
  font-size: 14px;
  color: #777;
  position: relative;
  width: 100%;
  text-align: center;
}

.orDivider::before,
.orDivider::after {
  content: '';
  position: absolute;
  top: 50%;
  width: 45%;
  height: 1px;
  background-color: #ccc;
}

.orDivider::before {
  left: 0;
}

.orDivider::after {
  right: 0;
}

.googleButton {
  width: 100%;
  padding: 10px;
  background-color: #db4437;
  color: #fff;
  border: none;
  border-radius: 4px;
  font-size: 16px;
  cursor: pointer;
}

.googleButton:hover {
  background-color: #c1351d;
}

.policyCheckbox {
  margin: 15px 0;
  padding: 10px;
  background-color: #f8f9fa;
  border-radius: 5px;
}

.policyCheckbox label {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 14px;
  cursor: pointer;
}

.policyCheckbox input[type="checkbox"] {
  width: auto;
  margin-right: 8px;
}

.policyLink {
  background: none;
  border: none;
  color: #007bff;
  text-decoration: underline;
  cursor: pointer;
  padding: 0;
  font-size: 14px;
}

.policyLink:hover {
  color: #0056b3;
}

/* Loading spinner styles */
@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.loadingSpinner {
  display: inline-block;
  width: 16px;
  height: 16px;
  border: 2px solid rgba(255, 255, 255, 0.3);
  border-radius: 50%;
  border-top-color: #fff;
  animation: spin 1s ease-in-out infinite;
  margin-right: 8px;
}