.orderHistoryContainer {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  color: #333;
}

.orderHistoryContainer h1 {
  text-align: center;
  font-size: 36px;
  margin-bottom: 20px;
  color: #2c3e50;
}

.loading {
  text-align: center;
  font-size: 18px;
  color: #007bff;
  margin-top: 20px;
}

.error {
  text-align: center;
  font-size: 18px;
  color: #dc3545;
  margin-top: 20px;
}

.ordersTable {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
}

.ordersTable th,
.ordersTable td {
  padding: 12px;
  text-align: left;
  border-bottom: 1px solid #ddd;
}

.ordersTable th {
  background-color: #f8f9fa;
  font-weight: 600;
}

.ordersTable td ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.ordersTable td ul li {
  margin-bottom: 5px;
}

.trackButton,
.cancelButton {
  padding: 8px 12px;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-size: 14px;
  transition: background-color 0.3s ease;
}

.trackButton {
  background-color: #007bff;
  color: white;
}

.trackButton:hover {
  background-color: #0056b3;
}

.cancelButton {
  background-color: #dc3545;
  color: white;
  margin-left: 10px;
}

.cancelButton:hover {
  background-color: #c82333;
}

.alert {
  position: fixed;
  top: 20px;
  right: 20px;
  padding: 15px;
  border-radius: 8px;
  color: white;
  font-size: 14px;
  z-index: 1000;
  animation: slideIn 0.5s ease;
}

.alert.success {
  background-color: #28a745;
}

.alert.error {
  background-color: #dc3545;
}

@keyframes slideIn {
  from {
    transform: translateX(100%);
  }
  to {
    transform: translateX(0);
  }
}

/* Responsive Design */
@media (max-width: 768px) {
  .ordersTable {
    display: block;
    overflow-x: auto;
  }
}