.orderConfirmedContainer {
  padding: 2rem;
  background-color: #f9f9f9;
  min-height: 100vh;
  text-align: center;
}

.confirmationAnimation {
  margin-bottom: 2rem;
}

.checkmark {
  font-size: 5rem;
  color: #28a745;
}

.orderConfirmedContainer h1 {
  font-size: 2.5rem;
  color: #333;
  margin-bottom: 1rem;
}

.orderConfirmedContainer p {
  font-size: 1.2rem;
  color: #666;
  margin-bottom: 1rem;
}

.orderDetails {
  background: #fff;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  padding: 2rem;
  margin: 2rem auto;
  max-width: 600px;
  text-align: left;
}

.orderDetails h2 {
  font-size: 1.8rem;
  color: #333;
  margin-bottom: 1rem;
}

.products {
  margin-bottom: 1.5rem;
}

.product {
  margin-bottom: 0.5rem;
}

.deliveryAddress {
  margin-top: 1.5rem;
}

.deliveryAddress h2 {
  font-size: 1.5rem;
  color: #333;
  margin-bottom: 1rem;
}

.cancelButton {
  background: #dc3545;
  color: #fff;
  border: none;
  border-radius: 5px;
  padding: 0.75rem 1.5rem;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: background 0.3s ease, transform 0.3s ease;
}

.cancelButton:hover {
  background: #c82333;
  transform: scale(1.05);
}

.loading {
  font-size: 1.5rem;
  color: #007bff;
}

.error {
  font-size: 1.5rem;
  color: #dc3545;
}