.featuredProducts {
    padding: 2rem;
    text-align: center;
    background-color: #f9f9f9;
  }
  
  .featuredProducts h2 {
    font-size: 2.5rem;
    margin-bottom: 2rem;
    color: #333;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 1px;
  }
  
  .productGrid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 2rem;
    padding: 1rem;
  }
  
  .productCard {
    background: #fff;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    overflow: hidden;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
  }
  
  .productCard:hover {
    transform: translateY(-10px);
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
  }
  
  .productImageContainer {
    width: 100%;
    height: 200px;
    overflow: hidden;
    position: relative;
  }
  
  .productImage {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: transform 0.3s ease;
  }
  
  .productCard:hover .productImage {
    transform: scale(1.1);
  }
  
  .productCard h3 {
    font-size: 1.5rem;
    margin: 1rem 0;
    color: #333;
    font-weight: 600;
  }
  
  .productCard p {
    font-size: 1.2rem;
    color: #666;
    margin-bottom: 1rem;
  }
  
  .priceOptions {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    gap: 0.5rem;
    margin-bottom: 1rem;
  }
  
  .quantityButton {
    background: #fff;
    border: 2px solid #007bff;
    color: #007bff;
    padding: 0.5rem 1rem;
    border-radius: 5px;
    cursor: pointer;
    transition: background 0.3s ease, color 0.3s ease;
    font-weight: 600;
  }
  
  .quantityButton:hover {
    background: #007bff;
    color: #fff;
  }
  
  .quantityButton.selected {
    background: #007bff;
    color: #fff;
  }
  
  .addToCartButton {
    width: 80%;
    margin: 1rem auto;
    padding: 0.75rem;
    background: #28a745;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 1rem;
    font-weight: 600;
    transition: background 0.3s ease, transform 0.3s ease;
  }
  
  .addToCartButton:hover {
    background: #218838;
    transform: scale(1.05);
  }
  
  .shopMoreButton {
    display: inline-block;
    margin-top: 2rem;
    padding: 1rem 2rem;
    background: #007bff;
    color: #fff;
    text-decoration: none;
    border-radius: 5px;
    font-size: 1.2rem;
    font-weight: 600;
    transition: background 0.3s ease, transform 0.3s ease;
  }
  
  .shopMoreButton:hover {
    background: #0056b3;
    transform: translateY(-3px);
  }
  
  /* Responsive Design */
  @media (max-width: 768px) {
    .productGrid {
      grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    }
  
    .productCard h3 {
      font-size: 1.3rem;
    }
  
    .productCard p {
      font-size: 1rem;
    }
  
    .quantityButton {
      padding: 0.4rem 0.8rem;
      font-size: 0.9rem;
    }
  
    .addToCartButton {
      font-size: 0.9rem;
    }
  
    .shopMoreButton {
      font-size: 1rem;
      padding: 0.8rem 1.5rem;
    }
  }