/* Checkout.module.css */

.checkoutContainer {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  font-family: Arial, sans-serif;
}

.orderSummary {
  margin-bottom: 30px;
}

.orderSummary table {
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 20px;
}

.orderSummary th,
.orderSummary td {
  padding: 10px;
  text-align: left;
  border-bottom: 1px solid #ddd;
}

.orderSummary th {
  background-color: #f5f5f5;
}

.totals {
  text-align: right;
  margin-top: 20px;
}

.totals p {
  margin: 5px 0;
}

.addressSection {
  margin-bottom: 30px;
}

.addressSection h2 {
  margin-bottom: 10px;
}

.addressSection button {
  background-color: #007bff;
  color: white;
  border: none;
  padding: 10px 20px;
  cursor: pointer;
  border-radius: 5px;
  font-size: 16px;
}

.addressSection button:hover {
  background-color: #0056b3;
}

.selectedAddress {
  background-color: #f9f9f9;
  padding: 15px;
  border: 1px solid #ddd;
  border-radius: 5px;
  margin-bottom: 20px;
}

.selectedAddress p {
  margin: 0;
}

.selectedAddress button {
  margin-top: 10px;
  background-color: #6c757d;
}

.selectedAddress button:hover {
  background-color: #5a6268;
}

.addressModal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modalContent {
  background-color: white;
  padding: 20px;
  border-radius: 10px;
  max-width: 500px;
  width: 100%;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.modalContent h3 {
  margin-top: 0;
}

.modalContent ul {
  list-style: none;
  padding: 0;
}

.modalContent ul li {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 0;
  border-bottom: 1px solid #ddd;
}

.modalContent ul li:last-child {
  border-bottom: none;
}

.modalContent ul li button {
  background-color: #28a745;
  color: white;
  border: none;
  padding: 5px 10px;
  cursor: pointer;
  border-radius: 5px;
}

.modalContent ul li button:hover {
  background-color: #218838;
}

.modalContent button {
  margin-top: 10px;
  background-color: #dc3545;
  color: white;
  border: none;
  padding: 10px 20px;
  cursor: pointer;
  border-radius: 5px;
}

.modalContent button:hover {
  background-color: #c82333;
}

.addressForm {
  margin-top: 20px;
}

.addressForm input,
.addressForm textarea {
  width: 100%;
  padding: 10px;
  margin-bottom: 10px;
  border: 1px solid #ddd;
  border-radius: 5px;
  font-size: 16px;
}

.addressForm textarea {
  resize: vertical;
  height: 100px;
}

.addressForm button {
  background-color: #007bff;
  color: white;
  border: none;
  padding: 10px 20px;
  cursor: pointer;
  border-radius: 5px;
  font-size: 16px;
  margin-right: 10px;
}

.addressForm button[type="button"] {
  background-color: #6c757d;
}

.addressForm button:hover {
  background-color: #0056b3;
}

.addressForm button[type="button"]:hover {
  background-color: #5a6268;
}

.overviewSection {
  text-align: center;
  margin-top: 30px;
}

.overviewSection p {
  margin-bottom: 20px;
}

.confirmOrderButton {
  background-color: #28a745;
  color: white;
  border: none;
  padding: 15px 30px;
  cursor: pointer;
  border-radius: 5px;
  font-size: 18px;
}

.confirmOrderButton:disabled {
  background-color: #6c757d;
  cursor: not-allowed;
}

.confirmOrderButton:hover:not(:disabled) {
  background-color: #218838;
}

.error {
  color: #dc3545;
  margin-bottom: 20px;
}